import styled from "@emotion/styled/macro";
import PlayArrowRoundedIcon from '@mui/icons-material/PlayArrowRounded';
import { useState } from "react";
import { SkipNext, SkipPrevious } from "@mui/icons-material";
import axios from "axios";


export const Video = () => {
    const [played, setPlayed] = useState(false)
    const handlePlay = async () => {
        setPlayed(true)
    }

    const [src, setSrc] = useState(1)
    const handleNext = () => {
        setSrc(src+1)
    }

    const handlePrev = () => {
        if (src===1){} else setSrc(src-1)
    }
    return(
        <Con>
            {played ?
                <ActualVideo src={"https://firebasestorage.googleapis.com/v0/b/animebruh-91c26.appspot.com/o/1.mp4?alt=media&token=88af563c-6d8f-4ee0-87a0-7e7c07d5bac8"} autoPlay controls>
                </ActualVideo> : 
                <Wrapper>
                    <Img src="/slaya.jpg"/>
                    <Play onClick={handlePlay}>
                    <PlayArrowRoundedIcon className="play"/>
                    </Play>
                </Wrapper>}
            <Buttons>
                <button onClick={handlePrev}>
                    <SkipPrevious/>
                    Prev
                </button>
                
                <button onClick={handleNext}>
                    <SkipNext/>
                    Next
                </button>
            </Buttons>
            <CurrentEpisode>
                You're watching<span> Episode 4</span>.
            </CurrentEpisode>
        </Con>
    )
}

const Con = styled.div`
    flex-grow: 1;
    height: fit-content;
    position: relative;
`

const Wrapper = styled.div`
    position: relative;
`
const Play = styled.button`
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    padding: 0;
    border: none;
    background-color: transparent;
    border-radius: 50%;
    svg{
        @media (max-width: 1450px){
            height: 70px;
            width: 70px;
        }
        color: #dddddd;
        height: 80px;
        width: 80px;
        background-color: #5a2e98;
        border-radius: 50%;
        padding: 4px;
        border: 5px solid #dddddd;
        
        transition: all 150ms;
        cursor: pointer;
        &:hover{
            transform: scale(1.075);
            color: white;
            border-color: white;
        }
    }
`

const Img = styled.img`
    display: block;
    width: 100%;
`

const Buttons = styled.div`
    display: flex;
    align-items: center;
    background-color: #010101;
    button{
        background-color: transparent;
        border: none;
        color: #dddddd;
        font-family: "Nunito";
        display: flex;
        align-items: center;
        cursor: pointer;

        svg{
            display: block;
        }
    }
`

const ActualVideo = styled.video`
    width: 1094px;
    display: block;
    width: 100%;
    height: 100%/16*9;
`

const CurrentEpisode = styled.div`
    background-color: #1c1c1c;
    color: #aaaaaa;
    height: 40px;
    text-align: center;
    font-size: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    span{
        color: #8c5ece;
        font-weight: 700;
        margin-left: 3px;
    }
`

