import styled from "@emotion/styled/macro"

export const Episodes = (type) => {
    return(
        <Con>
            <Episode>1</Episode>
            <Episode>2</Episode>
            <Episode>3</Episode>
            <Episode>4</Episode>
            <Episode>5</Episode>
            <Episode>6</Episode>
            <Episode>7</Episode>
            <Episode>8</Episode>
            <Episode>9</Episode>
            <Episode>10</Episode>
            <Episode>11</Episode>
            <Episode>12</Episode>
            <Episode>13</Episode>
            <Episode>14</Episode>
            <Episode>15</Episode>
            <Episode>16</Episode>
            <Episode>17</Episode>
            <Episode>18</Episode>
            <Episode>19</Episode>
            <Episode>20</Episode>
            <Episode>21</Episode>
            <Episode>22</Episode>
            <Episode>23</Episode>
            <Episode>24</Episode>
            <Episode>25</Episode>
            <Episode>26</Episode>
        </Con>
    )
}

const Con = styled.div`
    grid-template-columns: repeat(20, 1fr);
    @media (max-width: 1200px){
        grid-template-columns: repeat(10, 1fr);
    @media (max-width: 575px){
        grid-template-columns: repeat(5, 1fr);
    }
    }
    padding: 10px;
    background-color: #010101;
    display: grid;
    gap: 4px;
`

const Episode = styled.div`
    color: #777777;
    text-align: center;
    background-color: #171717;
    border-radius: 2px;
    font-size: 12px;
    font-weight: 600;
    height: 27.5px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 1px;
`

